/* ==========================
 *     FANCYBOX v2.1.5
 * ========================*/

.fancybox-wrap, .fancybox-skin, .fancybox-outer, .fancybox-inner, .fancybox-image {
    padding: 0;
    margin: 0;
    border: 0;
    outline: none;
    vertical-align: top; }

.fancybox-wrap {
    iframe, object {
        padding: 0;
        margin: 0;
        border: 0;
        outline: none;
        vertical-align: top; } }

.fancybox-nav {
    padding: 0;
    margin: 0;
    border: 0;
    outline: none;
    vertical-align: top;
    span {
        padding: 0;
        margin: 0;
        border: 0;
        outline: none;
        vertical-align: top; } }

.fancybox-tmp {
    padding: 0;
    margin: 0;
    border: 0;
    outline: none;
    vertical-align: top; }

.fancybox-wrap {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 19020; }

.fancybox-skin {
    position: relative;
    background: #fff;
    text-shadow: none;
    padding: 0 !important; }
.fancybox-opened {
    z-index: 19030; }

.fancybox-outer {
    position: relative; }

.fancybox-inner {
    position: relative;
    overflow: hidden; }

.fancybox-type-iframe .fancybox-inner {
    -webkit-overflow-scrolling: touch; }

.fancybox-error {
    color: #444;
    font: 14px/20px "Helvetica Neue",Helvetica,Arial,sans-serif;
    margin: 0;
    padding: 15px;
    white-space: nowrap; }

.fancybox-image, .fancybox-iframe {
    display: block;
    width: 100%;
    height: 100%; }

.fancybox-image {
    max-width: 100%;
    max-height: 100%; }

#fancybox-loading {
    position: fixed;
    top: 50%;
    left: 50%;
    margin-top: -22px;
    margin-left: -22px;
    background-position: 0 -108px;
    opacity: 0.8;
    cursor: pointer;
    z-index: 19060;
    div {
        width: 44px;
        height: 44px; } }
.fancybox-close {
    position: absolute;
    top: 15px;
    right: 15px;
    width: 11px;
    height: 10px;
    cursor: pointer;
    z-index: 19040;
    background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAsAAAAKCAYAAABi8KSDAAAAPUlEQVQoU2NkYGD4zwABjFAaGwVWA1OATwNcDtk0bBpQxNCtRpbE0IzNnTBFGP6gSDHRziDag0QHHdGRAgDpJxIJHTijBwAAAABJRU5ErkJggg=='); }

.fancybox-nav {
    position: absolute;
    top: 0;
    width: 40%;
    height: 100%;
    cursor: pointer;
    text-decoration: none;
    background: transparent url('blank.gif');
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    z-index: 19040; }

.fancybox-prev {
    left: 0; }

.fancybox-next {
    right: 0; }

.fancybox-nav span {
    position: absolute;
    top: 50%;
    width: 36px;
    height: 34px;
    margin-top: -18px;
    cursor: pointer;
    z-index: 19040;
    visibility: hidden; }

.fancybox-prev span {
    left: 10px;
    background-position: 0 -36px; }

.fancybox-next span {
    right: 10px;
    background-position: 0 -72px; }

.fancybox-nav:hover span {
    visibility: visible; }

.fancybox-tmp {
    position: absolute;
    top: -99999px;
    left: -99999px;
    visibility: hidden;
    max-width: 99999px;
    max-height: 99999px;
    overflow: visible !important; }

/* Overlay helper */

.fancybox-lock {
    overflow: hidden !important;
    width: auto;
    body {
        overflow: hidden !important; } }

.fancybox-lock-test {
    overflow-y: hidden !important; }

.fancybox-overlay {
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    display: none;
    z-index: 19010;
    background: rgba(0,0,0,0.7); }

.fancybox-overlay-fixed {
    position: fixed;
    bottom: 0;
    right: 0; }

.fancybox-lock .fancybox-overlay {
    overflow: auto;
    overflow-y: scroll; }

/* Title helper */

.fancybox-title {
    visibility: hidden;
    font: normal 13px/20px "Helvetica Neue",Helvetica,Arial,sans-serif;
    position: relative;
    text-shadow: none;
    z-index: 19050; }

.fancybox-opened .fancybox-title {
    visibility: visible; }

.fancybox-title-float-wrap {
    position: absolute;
    bottom: 0;
    right: 50%;
    margin-bottom: -35px;
    z-index: 19050;
    text-align: center;
    .child {
        display: inline-block;
        margin-right: -100%;
        padding: 2px 20px;
        background: transparent;
        /* Fallback for web browsers that doesn't support RGBa */
        background: rgba(0, 0, 0, 0.8);
        -webkit-border-radius: 15px;
        -moz-border-radius: 15px;
        border-radius: 15px;
        text-shadow: 0 1px 2px #222;
        color: #FFF;
        font-weight: bold;
        line-height: 24px;
        white-space: nowrap; } }

.fancybox-title-outside-wrap {
    position: relative;
    margin-top: 10px;
    color: #fff; }

.fancybox-title-inside-wrap {
    padding-top: 10px; }

.fancybox-title-over-wrap {
    position: absolute;
    bottom: 0;
    left: 0;
    color: #fff;
    padding: 10px;
    background: #000;
    background: rgba(0, 0, 0, 0.8); }

/*Retina graphics! */
@media only screen and (-webkit-min-device-pixel-ratio: 1.5), only screen and (min--moz-device-pixel-ratio: 1.5), only screen and (min-device-pixel-ratio: 1.5) {
   .fancybox-close {
        @include scale(1.2);
        background: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAsAAAAKCAYAAABi8KSDAAAAPUlEQVQoU2NkYGD4zwABjFAaGwVWA1OATwNcDtk0bBpQxNCtRpbE0IzNnTBFGP6gSDHRziDag0QHHdGRAgDpJxIJHTijBwAAAABJRU5ErkJggg=='); } }
