.notify {
	position: relative;
	min-height: 50px;
	display: flex;
	flex-direction: row;
	text-align: left;
	margin-top: 10px;

	&__photo {
		margin-right: 10px;
		width: 50px;
		height: 50px;
		border-radius: 50%; }

	&__info {
		margin-top: 5px; }

	&__date {
		color: #666666;
		font-size: 14px; }

	&__text {
		margin-top: 5px;
		font-size: 14px;
		line-height: 16px; }

	&__sum {
		font-size: 12px;
		line-height: 16px;
		margin-left: 10px;
		border-radius: 8px;
		background: #bda180;
		color: #fff;
		padding: 0 8px; }

	&__link {
		color: #bda180;
		font-size: 14px;
		&:hover {
			text-decoration: underline; } }

	&__comment {
		position: relative;
		display: inline-block;
		margin-top: 18px;
		padding: 17px 13px;
		border-radius: 3px;
		border-top-left-radius: 0;
		border: 1px solid #e5e5e5;
		&:before {
			position: absolute;
			top: -9px;
			left: -1px;
			content: "";
			width: 10px;
			height: 10px;
			background: url("../img/notify-comment-angle.jpg"); } } }

.fixed .banners-main {
	display: none; }

@media screen and (max-width:767px) {
	.banners-main {
		left: 0px;
		right: 0px; } }

@media screen and (max-width:600px) {
	.banners-main--mobile {
		display: block;
		position: relative;
		left: auto;
		right: auto;
		top: auto;
		bottom: auto; }

	.banners-main--desktop {
		display: none; }

	.banners-main__item {
		width: 100%;
		/*height: 80px*/ }
	.banners-main__item:last-child {
		width: 100%; } }
