/* ==========================
 *     SLICK SLIDER
 * ========================*/
.slick-slider {
    position: relative;
    display: block;
    box-sizing: border-box;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    -webkit-touch-callout: none;
    -khtml-user-select: none;
    -ms-touch-action: pan-y;
    touch-action: pan-y;
    -webkit-tap-highlight-color: transparent; }

.slick-list {
    position: relative;
    display: block;
    overflow: hidden;
    margin: 0;
    padding: 0;
    &:focus {
        outline: none; }
    &.dragging {
        cursor: pointer;
        cursor: hand; } }

.slick-slider {
    .slick-track, .slick-list {
        -webkit-transform: translate3d(0, 0, 0);
        -moz-transform: translate3d(0, 0, 0);
        -ms-transform: translate3d(0, 0, 0);
        -o-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0); } }

.slick-track {
    position: relative;
    top: 0;
    left: 0;
    display: block;
    &:before {
        display: table;
        content: ''; }
    &:after {
        display: table;
        content: '';
        clear: both; } }

.slick-loading .slick-track {
    visibility: hidden; }

.slick-slide {
    display: none;
    float: left;
    height: 100%;
    min-height: 1px; }

[dir='rtl'] .slick-slide {
    float: right; }

.slick-slide {
    img {
        display: block; }
    &.slick-loading img {
        display: none; }
    &.dragging img {
        pointer-events: none; } }

.slick-initialized .slick-slide {
    display: block; }

.slick-loading .slick-slide {
    visibility: hidden; }

.slick-vertical .slick-slide {
    display: block;
    height: auto;
    cursor: pointer; }

.slick-arrow.slick-hidden {
    display: none; }

// Arrows
.slick-arrow {
    position: absolute;
    top: 50%;
    z-index: 1000;
    width: 46px;
    height: 78px;
    margin-top: -57px;
    text-indent: -9999px;
    overflow: hidden;
    border: none;
    padding: 0;
    background: transparent url(../img/sprite.png) no-repeat 0 0;
    @include opacity(0.44);
    @include tr(all 0.4s);
    &:hover {
        @include opacity(1); } }

.slick-prev {
    left: 5px;
    z-index: 1000;
    background-position: 0 -40px;
    @include rotate(180); }

.slick-next {
    right: 5px;
    z-index: 1000;
    background-position: 0 -40px; }
