// done by Coderiver.com.ua
@import "lib/base";
@import "normalize";
@import "slick";
@import "styled";
@import "fancybox";
// @import main

/* BEM */
/* ================================================ */

@import "bem/banners-main";
@import "bem/notify";


$color-default: #e5e5e5;

$color-warning: #c1a788;

/* 3. MAIN STYLES */
/* ================================================ */

body.fixed-header {
	padding-top: 110px; }

body.fixed {
	overflow: hidden; }

.main-wrapper {
	padding: 0 0 0 0;
	min-width: 1210px;
	position: relative;
	min-height: 100%; }


.wrapper {
	width: 1210px;
	padding: 0 10px 0 10px;
	margin: 0 auto;
	position: relative; }


/* titles */
h1 {
	font-size: 24px;
	line-height: 28px;
	padding: 7px 0 7px 0;
	font-weight: 400;
	color: #1f1611;
	.link {
		display: inline-block;
		border-bottom: 1px solid transparent;
		&:hover {
			border-bottom: 1px solid; } } }

h2 {
	font-size: 1.8em;
	line-height: 1.1em;
	padding: 7px 0 7px 0;
	font-weight: 400;
	color: #1f1611; }

h3 {
	font-size: 24px;
	line-height: 28px;
	padding: 7px 0 7px 0;
	font-weight: 400;
	color: #1f1611; }
h4 {
	font-size: 18px;
	line-height: 22px;
	padding: 7px 0 7px 0;
	font-weight: 400;
	color: #1f1611; }


h6 {
	font-size: 14px;
	line-height: 20px;
	padding: 7px 0 7px 0;
	font-weight: 400;
	color: #1f1611; }
p {
	font-size: 14px;
	line-height: 18px;
	padding: 7px 0 7px 0;
	font-weight: 400;
	color: #1f1611;
	.link {
		line-height: 14px;
		vertical-align: middle; } }

span.kbd {
	display: inline-block;
	margin: 0 0 0 2px;
	&--add-info {
		color: #bda180; }
	&--lk-menu {
		margin-left: 7px; } }



ol {
	list-style-position: inside;
	line-height: 18px; }

table {
	width: 100%;
	margin: 13px 0 16px;
	td, th {
		text-align: left;
		font-weight: 400;
		padding: 13px 13px;
		&.td-date {
			width: 90px; }
		&.td-date-time {
			width: 140px; }
		&.td-summa {
			width: 175px; } }
	th {
		color: #ababab; }
	td {
		border-top: 1px solid $color-default; }
	tr:last-child td {
		border-bottom: 1px solid $color-default; } }



.delspin {
	display: inline-block;
	vertical-align: top;
	& + .delspin {
		margin-left: 7px; } }

/* text position */

.text-left {
	text-align: left !important; }

.text-center {
	text-align: center !important; }

.text-right {
	text-align: right !important; }

.text-warning {
	color: $color-warning; }
.nowrap {
	white-space: nowrap !important; }
ol.list {
	list-style-type: decimal;
	line-height: 17px; }

.fl {
	float: left !important; }
.fr {
	float: right !important; }

.mt10 {
	margin-top: 10px !important; }
.mt5 {
	margin-top: 5px !important; }
.mt7 {
	margin-top: 7px !important; }
.mb30 {
	margin-bottom: 30px !important; }
.mb25 {
	margin-bottom: 25px !important; }
.mb20 {
	margin-bottom: 20px !important; }
.hidden {
	display: none !important; }
/* loader */
.loaded .main-wrapper {
	visibility: hidden; }

.icon-load {
	background: url(../img/loader.gif) no-repeat left top;
	width: 40px;
	height: 40px;
	position: fixed;
	left: 50%;
	top: 50%;
	margin-left: -20px;
	margin-left: -20px;
	display: none; }

.loaded .icon-load {
	display: block; }



/* 3.1. Icons
     @mixin ===================== */
.ic {
	top: -1px;
	position: relative;
	display: inline-block;
	vertical-align: middle;
	background: url(../img/sprite.png) no-repeat 0 0;
	&_flag-ru {
		top: -2px;
		width: 18px;
		height: 12px;
		background-position: 0 0; }
	&_flag-en {
		top: -2px;
		width: 18px;
		height: 12px;
		background-position: 0 -12px; }
	&_models-two {
		width: 13px;
		height: 12px;
		background-position: -18px 0; }
	&_models-one {
		width: 12px;
		height: 12px;
		background-position: -18px -12px; }
	&_models-three {
		width: 12px;
		height: 12px;
		background-position: -18px -24px; }
	&_corona {
		width: 18px;
		height: 8px;
		background-position: -37px 0; }
	&_play {
		width: 6px;
		height: 11px;
		background-position: -30px 0; }
	&_arrow {
		width: 21px;
		height: 11px;
		background-position: -31px -14px; }
	&_eye {
		width: 27px;
		height: 12px;
		background-position: -56px 0px; } }



/* 3.2. Buttons
     @mixin ===================== */
.button, a.button, button.button {
	position: relative;
	display: inline-block;
	vertical-align: middle;
	height: 34px;
	padding: 7px 34px 0;
	font: 400 17px/18px 'MyriadPro', Arial, sans-serif;
	color: #fff;
	text-align: center;
	text-decoration: none;
	border: none;
	background-color: #eee;
	cursor: pointer;
	@include br(4px);
	@include tr(all 0.2s linear);
	&_block {
		width: 100%;
		display: block;
		padding-right: 3px;
		padding-left: 3px; }
	&_lg {
		height: 57px;
		padding: 18px 41px 0;
		border-radius: 0; }
	&_moderate {
		width: 220px; }
	&_warning {
		background-color: $color-warning;
		&:hover {
			background-color: #A38D73;
			color: #fff;
			text-decoration: none; } }
	& + .help-text {
		margin-left: 20px; }
	&.main-button {
		width: 182px;
		margin-top: 30px;
		margin-bottom: 5px; } }
.box-button {
	margin: 12px 0;
	.button + .button-info {
		display: inline-block;
		margin-left: 20px; } }

input.button,
button.button {
	padding-top: 0 !important;
	padding-bottom: 2px !important; }

// button group
.btn-group {
	.button + .button {
		margin-left: 15px; }
	&_vertical {
		.button + .button {
			margin-top: 15px; } } }


/* 3.3. forms
     @mixin ===================== */
input[type='text'],
input[type='number'],
input[type='email'],
input[type='tel'],
input[type='search'],
input[type='url'],
input[type='password'],
textarea,
select {
	height: 33px;
	margin-bottom: 15px;
	padding: 0 10px;
	color: #000;
	font: normal 14px/18px 'MyriadPro', Arial, sans-serif;
	border: 1px solid #ccc;
	background: #fff;
	@include br(4px);
	&.form-control {
		display: block;
		width: 100%; }
	&:focus {
		background-color: #f7f7f7; }
	@include placeholder {
		color: #aaa; } }

textarea {
	height: auto;
	min-height: 85px;
	padding-top: 10px;
	resize: vertical;
	font: normal 14px/18px 'MyriadPro', Arial, sans-serif; }

label {
	display: block;
	font-weight: 400;
	font-size: 14px;
	color: #666666;
	margin-bottom: 5px; }

.help-text {
	color: rgba(0,0,0,0.4);
	font-size: 14px;
	font-weight: 400;
	line-height: 24px;
	&.inline {
		display: inline-block;
		vertical-align: middle; } }

.box-select-group {
	border: 1px solid #ccc;
	@include br(4px);
	@include clr;
	.jq-selectbox {
		display: inline-block;
		vertical-align: top;

		&.col-3 {
			width: 33.33%;
			display: block;
			float: left; }
		&.focused .jq-selectbox__select {
			border: none; } }

	.jq-selectbox__select {
		border: none; } }

.box-field-line {
	position: relative;
	height: 1px;
	&__line {
		position: absolute;
		left: -22px;
		right: -22px;
		border-top: 1px solid #e5e5e5; } }

.box-field-title {
	color: #000000;
	font-size: 18px;
	margin-bottom: 20px;
	.box-field-line + & {
		margin-top: 20px; } }

.box-field-group {
	@include clr;
	.box-field {
		float: left;
		& + .box-field {
			margin-left: 2%; } } }


.box-field {
	margin-bottom: 30px;
	&.col-5 {
		width: 61%; }
	&.col-4 {
		width: 40.3%; }
	&.col-3 {
		width: 26%; }
	&.col-2 {
		width: 23%; }
	&.col-1 {
		width: 13.5%; }
	input,
	textarea,
	select {
		margin-bottom: 0 !important; } }




.checkbox, .radio {
	label {
		margin-left: -20px;
		padding-left: 26px;
		display: inline-block;
		vertical-align: top;
		// width: 100%
		line-height: 18px;
		font-size: 14px;
		.link {
			display: inline; } } }

.captcha {
	margin: 28px 0 30px; }

.settings-buttons-form {
	margin-bottom: 20px; }

.box-label {
	&__title {
		position: relative;
		z-index: 1;
		display: inline-block;
		vertical-align: middle;
		font-size: 18px;
		line-height: 22px;
		width: 210px;
		margin-right: -4px;
		margin-bottom: 40px; }

	&__cont {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		margin-left: -210px;
		padding-left: 215px;
		margin-right: -4px;
		margin-bottom: 40px;
		font-size: 18px;
		line-height: 24px;
		.link + .link {
			margin-left: 60px; }
		small {
			font-size: 14px;
			line-height: 18px; }
		input.box-label__input {
			position: relative;
			top: 2px;
			margin-bottom: 0;
			padding: 0 8px;
			margin-left: -6px;
			font-size: 22px;
			width: 118px; } }
	&.small {
		margin-top: 20px;
		.box-label__title {
			width: 230px;
			margin-bottom: 28px; }
		.box-label__cont {
			margin-left: -230px;
			padding-left: 243px;
			margin-bottom: 28px;
			font-size: 14px;
			line-height: 18px; } } }

.count {
	font-size: 36px;
	line-height: 36px;
	font-weight: 400; }


.user {
	margin: 31px 0 13px;
	label {
		margin-bottom: 14px; }
	&__avatar {
		width: 100px;
		height: 100px;
		line-height: 100px;
		text-align: center;
		background-color: #f7f7f7;
		overflow: hidden;
		&--transparent {
			background: transparent; }
		img {
			display: inline-block;
			vertical-align: middle;
			max-width: 100px;
			width: 100%; } }

	&__add-avatar {
		.box-field__input + .box-field__input {
			margin-top: 23px; } } }


.box-field__checkbox {
	margin-top: 6px;
	margin-bottom: 7px;
	line-height: 18px;
	&--profile {
		margin-top: 14px;
		margin-bottom: 0; } }
.box-field__checkbox-col {
	display: inline-block;
	margin-right: 50px; }

/* 4. HEADER */
/* ================================================ */
header {
	position: relative;
	width: 100%;
	min-width: 320px;
	text-align: left;
	min-height: 100px;
	//position: absolute
	//left: 0px
	//top: 10px
	margin-top: 10px;
	z-index: 9999;
	&.fixed {
		position: fixed;
		top: 0;
		margin-top: 0;
		min-height: 50px;
		.header-top {
			border-bottom: 1px solid $color-default; }
		.box-nav-list {
			display: none; }
		.header-bottom {
			position: absolute;
			top: 0;
			left: 232px;
			right: 10px;
			border-left: 1px solid $color-default;
			border-top: none;
			border-bottom: 1px solid $color-default; }
		.navigation,
		.nav-profile {
			left: -223px;
			right: 0; } } }

// header bottom
.header {
	&-bottom {
		height: 50px;
		background: #fff;
		border-top: 1px solid $color-default; }
	&-table {
		display: table;
		width: 100%;
		border-collapse: collapse;
		&__item {
			display: table-cell;
			vertical-align: middle;
			text-align: center;
			padding: 0 15px;
			color: #2e2e2e;
			font-size: 14px;
			line-height: 13px;
			font-weight: 400;
			border-right: 1px solid $color-default;
			//&:hover
			//	background-color: #f7f7f7
			&:first-child {
				padding-left: 0;
				text-align: left; }
			&:last-child {
				padding-right: 0;
				border-right: none; }
			&.models {
				width: 126px;
				padding-left: 20px; }
			&.add-models {
				width: 152px; }
			&.link-login {
				width: 110px; }
			&.link-registr {
				width: 130px;
				padding-right: 20px;
				border-right: none; }
			&.mini-profile {
				width: 182px;
				padding: 0; }
			&.search {
				padding: 0;
				&:hover {
					background-color: transparent; } } } } }

.header-top {
	height: 50px;
	padding: 14px 20px;
	background: #fff;
	@include clr; }

.logo {
	width: 200px;
	float: left;
	font-size: 23px;
	line-height: 20px;
	text-transform: uppercase;
	&__svg {
		width: 180px; }
	strong {
		font-weight: 600; } }

.box-nav-list {
	float: right; }

// navigation
.nav-list {
	margin: 5px -5px 0;
	&__item {
		position: relative;
		display: inline-block;
		vertical-align: top;
		margin-right: -4px;
		padding: 0 10px 0 9px; }

	&__link {
		display: block;
		span:not(.nav-list__icon) {
			display: inline-block;
			border-bottom: 1px solid transparent;
			-moz-transition: all 0.2s linear;
			-o-transition: all 0.2s linear;
			-ms-transition: all 0.2s linear;
			-webkit-transition: all 0.2s linear;
			transition: all 0.2s linear; }
		&.arrow-down {
			padding-right: 18px; }
		&:hover span:not(.nav-list__icon) {
			border-bottom: 1px solid; } }
	&__icon {
		margin-left: 7px;
		display: inline-block; } }

.lang-list {
	position: absolute;
	top: 100%;
	right: 28px;
	z-index: 2;
	display: none;
	&__item {
		margin-bottom: 3px; } }

.arrow-down {
	display: block;
	padding-right: 16px;
	background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAsAAAAGCAYAAAAVMmT4AAAANklEQVQoU2NkYGD4zwABjFAaGwVWA1OATwNcDtk0bBpQxNCtRpbE0IzNnTBFGP7A5SmQBgw5AALrCgbjsJcXAAAAAElFTkSuQmCC") no-repeat right center;
	&--profile {
		background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAsAAAAGCAYAAAAVMmT4AAAANklEQVQoU2NkYGD4zwABjFAaGwVWA1OATwNcDtk0bBpQxNCtRpbE0IzNnTBFGP7A5SmQBgw5AALrCgbjsJcXAAAAAElFTkSuQmCC") no-repeat center center; } }

.models {
	&.active {
		background-color: #f7f7f7; }
	&__text {
		display: block; } }

// Search
.search {
	form {
		position: relative; }
	input.search__input {
		height: 49px;
		width: 100%;
		padding-right: 60px;
		text-align: left;
		margin-bottom: 0;
		border: none;
		border-right: 1px solid $color-default;
		background: transparent;
		@include placeholder {
			color: rgba(0,0,0,0.4); } }
	&__button {
		position: absolute;
		top: 0;
		right: 0;
		z-index: 2;
		height: 49px;
		width: 50px;
		padding: 0;
		border: none;
		// border-left: 1px solid $color-default
		background: #fff url(../img/svg/search.svg) no-repeat center; }

	&__filters-link {
		position: absolute;
		top: 18px;
		right: 56px;
		padding-right: 15px;
		z-index: 2;
		color: #2e2e2e;
		font-size: 14px;
		line-height: 13px;
		font-weight: 400;
		background: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAsAAAAGCAYAAAAVMmT4AAAANklEQVQoU2NkYGD4zwABjFAaGwVWA1OATwNcDtk0bBpQxNCtRpbE0IzNnTBFGP7A5SmQBgw5AALrCgbjsJcXAAAAAElFTkSuQmCC") no-repeat right center;
		cursor: pointer; }

	&__filters-count {
		color: #bda180; }

	&__filters-block {
		display: none;
		position: absolute;
		z-index: 100;
		top: 50px;
		right: 0;
		background: #fff;
		border-left: 1px solid $color-default;
		border-right: 1px solid $color-default;
		border-bottom: 1px solid $color-default; }

	&__results {
		display: none;
		position: absolute;
		z-index: 100;
		top: 50px;
		left: 0px;
		right: 0px;
		background: #fff;
		border-right: 1px solid $color-default;
		border-left: 1px solid $color-default;
		border-bottom: 1px solid $color-default;
		text-align: left;

		&-item {
			border-bottom: 1px solid $color-default;
			padding: 10px 0;
			width: 100%;
			display: inline-block;
			vertical-align: middle;
			img {
				display: inline-block;
				vertical-align: middle;
				margin-right: 10px;
				height: 30px; } } } }

// filters
.filters {
	display: flex;
	flex-direction: row;
	text-align: left;
	&__left {
		width: 257px;
		padding: 20px;
		border-right: 1px solid $color-default; }
	&__right {
		width: 200px;
		display: flex;
		flex-direction: column;
		justify-content: space-between; }
	&__sort {
		padding: 20px;
		border-bottom: 1px solid $color-default; }
	&__style {
		padding: 20px; }
	&__button {
		padding: 20px; }
	&__label {
		font-size: 16px;
		font-weight: bold;
		margin-bottom: 10px; }
	&__checkbox {
		display: flex;
		flex-direction: row;
		justify-content: space-between; }
	&__checkbox-wrapper {
		width: 200px; }
	&__checkbox-count {
		padding-top: 3px;
		font-size: 14px;
		color: #7f7f7f; } }



// mini profile
.mini-profile {
	&__img {
		position: relative;
		z-index: 2;
		display: inline-block;
		vertical-align: middle;
		width: 50px;
		height: 49px;
		line-height: 49px;
		&:hover {
			background-color: #f7f7f7; }
		img {
			display: inline-block;
			vertical-align: middle; } }
	&__link {
		display: inline-block;
		vertical-align: middle;
		width: 100%;
		height: 49px;
		line-height: 49px;
		margin-left: -50px;
		padding-left: 50px;
		// padding-right: 20px

		&-login-wrapper {
			position: relative;
			// padding-right: 30px
			display: flex; }

		&-login-name {
			display: block;
			width: 100%;
			padding-right: 10px;
			overflow: hidden;
			text-overflow: ellipsis;
			white-space: nowrap;
			text-align: left;
			&:hover {
				background-color: #f7f7f7; } }

		&-login-arrow {
			display: block;
			width: 60px;
			padding-left: 10px;
			padding-right: 10px;
			&:hover {
				background-color: #f7f7f7; } }

		.ic {
			position: absolute;
			top: 50%;
			right: 15px;
			margin-top: -7px; } }
	&.active {
		background-color: #f7f7f7; } }

// Navigation
.navigation,
.nav-profile {
	// display: none
	// position: absolute
	// top: 100%
	position: relative;
	border-top: 1px solid $color-default;
	background-color: #f7f7f7;
	z-index: 75;
	left: 10px;
	right: 10px;
	&--main-page {
		position: absolute;
		top: 100%; } }

.nav-profile {
	left: 0px;
	right: 0px;
	&--main-page {
		left: 10px;
		right: 10px; } }

.navigation {
	position: absolute;
	top: 100px;
	display: none;
	z-index: 100 !important;
	overflow: hidden;
	&-table {
		display: table;
		width: 100%;
		z-index: 5;
		position: relative;
		background-color: #f7f7f7;
		&__cell {
			display: table-cell;
			width: 229px;
			padding: 16px 10px 25px;
			border-right: 1px solid $color-default;
			&:last-child {
				width: 245px;
				border-right: none;
				padding-right: 20px; }
			&:first-child {
				padding-left: 20px; }
			&:first-child {
				width: 251px; } } }
	&__title {
		font-size: 18px;
		line-height: 22px;
		padding: 7px 0 0px 0;
		font-weight: 400;
		color: #1f1611; }
	&-list {
		margin-bottom: 10px;
		&__item {
			line-height: 20px; }
		&__link {
